import { Button,} from 'antd';
import React,{useState,useEffect}from 'react'
// import BreadcrumbContent from '../../BreadcrumbContent'
export default function EffectHook() {
  const [count,setCount] = useState(()=>{
    console.log(`count-useState 执行`);
    return 20;
  });
  useEffect(() => {
    document.title=`hook ${count}`})
  return (
    <div>
      
      {/* <BreadcrumbContent routeUrl={this.props.history.localtion.pathname}></BreadcrumbContent> */}
      <h1>关于Hook</h1>
      <h2>Hook 是 React 16.8 的新增特性。它可以让你在不编写 class 的情况下使用 state 以及其他的 React 特性。</h2>
      <hr />
      <h1>1.useState</h1>
      <p>js数组解构语法 const [variable1,variable2] = functingx()</p>
      <p>需要的参数:该state的初始值;方法返回值:当前state以及更新state的函数 </p>
      <p>state 读取 直接用state 名称 --{count}</p>
      <p>state 更新 <Button onClick={()=>setCount(count+1)}>更新state</Button></p>
      <hr />
      <h1>useEffect</h1>
      <p>使用useEffect 更新标题状态</p>
      <p><Button onClick={()=>{setCount(count+1)}}>更新</Button></p>
      <p>useEffect Hook 可以看做 componentDidMount，componentDidUpdate 和 componentWillUnmount 这三个函数的组合。</p>
      <p>useEffect 默认情况下在第一次渲染(componentDidMount)之后和每次更新之后的都会执行</p>
      <p>不用考虑‘挂载’，还是‘更新’</p>
      <p>每次我们重新渲染，都会生成新的 effect，替换掉之前的。某种意义上讲，effect 更像是渲染结果的一部分 —— 每个 effect “属于”一次特定的渲染。</p>
      <p>类式组件中某些情况如订阅消息。我们要经常注意是否需要在compnentDidUpdate进行数据的处理。</p>
      <p>使用了useEffect 在每次渲染后都会对前一个effect进行清理并调用新的effect</p>
      <p>此默认行为保证了一致性，避免了在 class 组件中因为没有处理更新逻辑而导致常见的 bug。</p>
      <hr />
      <h1>Hook规则</h1>
      <h2>只在最顶层使用 Hook</h2>
      <p>不要在循环，条件或嵌套函数中调用 Hook， 确保总是在你的 React 函数的最顶层调用他们。
        遵守这条规则，你就能确保 Hook 在每一次渲染中都按照同样的顺序被调用。这让 React 
        能够在多次的 useState 和 useEffect 调用之间保持 hook 状态的正确。</p>
      <p>当使用useState() 时React 如何知道去调用哪个 useState? 按照 Hook的调用顺序 要严格遵循上条规则</p>
      <hr />
      <h1>自定义Hook</h1>
    </div>

  )
}
