import React, { Component } from 'react';
import BreadcrumbContent from '../../BreadcrumbContent';
import ReactWEditor from 'wangeditor-for-react';
import E from 'wangeditor'
let editor = null
export default class EditorCp extends Component {
  state = {
    uploadImgSrc:'',
    count:1
  }
  componentDidMount(){
    editor = new E('#weditor')
    console.log('componentDidMount')
    editor.config.uploadImgServer = '/upload-img';
    editor.create()
    document.title=`hello${this.state.count}`
  }
  componentDidUpdate(){
    console.log('componentDidUpdate')
    document.title = `hello${this.state.count}`
  }
  imageHandle = (event)=>{
    console.log(event.target.files[0])
  }
  fileRef = React.createRef();
  upload = ()=>{  
    // console.log(this.fileRef.current.files[0])
    let {count} = this.state;
    count++;
    console.log(count)
    this.setState({count:count})
    // const data = this.fileRef.current.files[0];
    // let fileReader = new FileReader();
    // fileReader.readAsDataURL(data);
    // fileReader.onload = res =>{
    //   this.setState({uploadImgSrc:res.target.result})
    //   console.log(res.target.result);
    // }
  }
  render() {
    const {uploadImgSrc} = this.state;
    console.log('render')
    return (
      <div className="animated bounceInRight">
        <BreadcrumbContent routeUrl={this.props.history.location.pathname}/>

        <input ref={this.fileRef} onChange={this.imageHandle} type="file" /><button onClick={this.upload}>上传</button>
        <img src={uploadImgSrc} alt="" />
        <ReactWEditor/>
        <div id="weditor"></div>
      </div>
    )
  }
}
