import React, { Component } from 'react'

export default class Dog extends Component {
  render() {
    const mouse = this.props.mouse;
    return (
      <div>
        <img style={{width:50,height:50,position:'absolute',left:mouse.x+20 ,top:mouse.y+20}} src="/dog.jpg" alt="dog" />
      </div>
    )
  }
}
